.contri-title {
  font-size: 1.6rem;
  /* rem for font size */
  font-family: "Fira Sans";
  font-weight: 900;
  margin-bottom: 2vh;
  /* vh for margin */
  margin-top: 3vh;
  /* vh for margin */
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  max-width: 90%;
  /* vh for max-width */
}

.contri-heading {
  font-size: 1.4rem;
  /* rem for font size */
  font-family: "Fira Sans";
  font-weight: 600;
  margin-bottom: 2vh;
  /* vh for margin */
  margin-top: 3vh;
  /* vh for margin */
  margin-right: auto;
  margin-left: auto;
  text-align: left;
  max-width: 90%;
  /* vh for max-width */
}

.contri-image-container {
  text-align: center;
  margin: auto;
  max-width: 90%;
  /* vh for max-width */
}

.contri-image {
  position: relative;
  max-width: 100%;
  align-items: center;
}

.contri-mainpara {
  font-size: 1.1rem;
  /* rem for font size */
  letter-spacing: 0.02rem;
  /* rem for letter-spacing */
  font-weight: 400;
  line-height: 1.4;
  margin-bottom: 2vh;
  /* vh for margin */
  text-align: center;
  margin-right: 5%;
  margin-left: 5%;
  max-width: 90%;
  /* vh for max-width */
}

.contri-para {
  font-size: 1.5rem;
  /* rem for font size */
  letter-spacing: 0.02rem;
  /* rem for letter-spacing */
  font-weight: 400;
  line-height: 1.4;
  margin-bottom: 2vh;
  /* vh for margin */
  text-align: left;
  margin-right: auto;
  margin-left: 16vh;
  /* vh for left margin */
  max-width: 90%;
  /* vh for max-width */
}

.features {
  margin: auto;
  max-width: 90%;
  /* vh for layout width */
}