.button {
  min-width: 16.5rem;
  /* Better for consistency in width */
  width: auto;
  height: 5vh;
  /* Suitable for a button height relative to viewport */
  letter-spacing: 0.05rem;
  /* Typography-related */
  line-height: 5vh;
  /* Matches the height for better vertical alignment */
  padding: 1vh 3.5vh;
  /* Uses vh for responsive vertical padding */
  font-size: 1.2rem;
  /* Typography-related */
  background-color: gold;
  color: black;
  text-transform: uppercase;
  font-family: "Open Sans Condensed";
  font-weight: bolder;
  border: none;
  cursor: pointer;
  text-decoration: none;
  border-radius: 5vh;
  /* For better scaling on different screens */

  &:hover {
    background-color: black;
    color: white;
    border: 0.1rem solid black;
    /* Thin borders should be relative to text size */
  }

  &.scroll {
    background-color: black;
    color: white;
    font-size: 1rem;
    /* Typography */

    &:hover {
      background-color: white;
      color: black;
      border: 0.1rem solid red;
      /* Matches typography size */
    }
  }
}

.main-heading {
  height: 14vh;
  /* Use vh for height responsiveness */
  margin: auto;
  margin-bottom: 5vh;
  /* Responsive bottom margin */
  background-color: rgb(193, 220, 247);

  h1 {
    // font-size: 3.2rem;
    /* Responsive text size */
    font-family: Fira Sans;
    text-align: center;
    font-weight: 600;
  }
}