img {
  margin-bottom: 20px;
  overflow: hidden;
  height: auto;
  transform: translateX(30px);
}

h1,
h2,
h3,
p {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin-bottom: 23px;
  overflow: hidden;
  height: auto;
  transform: translateY(30px);
}

@media (max-width: 768px) {
  html {
    font-size: 1.5rem;
  }

  h1 {
    font-size: 1.7rem;
  }

  h2 {
    font-size: 1.5rem;
  }
}

[data-aos="custom-slideUp"] {
  opacity: 0;
  transition-property: transform, opacity;

  &.aos-animate {
    transform: translateY(30px);
    opacity: 0;
  }

  &.aos-animate {
    opacity: 0.2;
  }

  &.aos-animate {
    opacity: 1;
    transform: translateY(0px);
  }
}

[data-aos="custom-slideIn"] {
  opacity: 0;
  transition-property: transform, opacity;

  &.aos-animate {
    transform: translateX(30px) scale(0.2);
    opacity: 0;
  }

  &.aos-animate {
    opacity: 0.2;
  }

  &.aos-animate {
    opacity: 1;
    transform: translateX(0px) scale(1);
  }
}

[data-aos="custom-slideDown"] {
  opacity: 0;
  transition-property: transform, opacity;

  &.aos-animate {
    transform: translateY(-30px);
    opacity: 0;
  }

  &.aos-animate {
    opacity: 0.2;
  }

  &.aos-animate {
    opacity: 1;
    transform: translateY(0px);
  }
}