.git-button {
  min-width: 16.5rem;
  /* rem for width */
  width: auto;
  height: 5vh;
  /* vh for height */
  letter-spacing: 0.05rem;
  /* rem for typography */
  line-height: 5vh;
  /* Same as height for proper vertical alignment */
  padding: 1vh 3.5vh;
  /* vh for padding to adjust with viewport */
  font-size: 1.2rem;
  /* rem for font size */
  background-color: gold;
  color: black;
  text-transform: uppercase;
  font-family: "Open Sans Condensed";
  font-weight: bolder;
  border: none;
  cursor: pointer;
  text-decoration: none;
  border-radius: 5vh;
  /* vh for better button radius scaling */

  &:hover {
    background-color: black;
    color: white;
    border: 0.1rem solid black;
    /* rem for consistent border size */
  }

  &.scroll {
    background-color: black;
    color: white;
    font-size: 1rem;
    /* rem for font size */

    &:hover {
      background-color: white;
      color: black;
      border: 0.1rem solid red;
      /* rem for hover border */
    }
  }
}

.main-heading {
  height: 14vh;
  /* vh for height */
  margin: auto;
  margin-bottom: 2vh;
  /* vh for bottom margin */
  background-color: rgb(193, 220, 247);

  h1 {
    // font-size: 3.2rem;
    /* rem for heading size */
    text-align: center;
    font-weight: 600;
  }
}