.header1 {
  height: 10vh;
  width: 100%;
  display: flex;
  justify-content: space-between;
  transform: translateY(-3.7vh);
  background-color: rgba(19, 19, 19, 0.5);
  position: fixed;
  z-index: 100;

  .logo-container {
    height: auto;
    width: 12.3vh;

    .logo {
      height: 9.9vh;
      width: 19.7vh;
    }
  }

  .menu-button {
    display: none;
    font-size: 3vh;
    color: white;
    background: none;
    border: none;
    cursor: pointer;
    margin-right: 2vh;
  }

  nav {
    width: auto;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 0.6vh;
    padding-left: 6.2vh;

    .menu {
      display: flex;
      justify-content: flex-end;
      margin-top: 7vh;
      width: 100%;
      height: 100%;
      list-style: none;
      max-width: 88.9vh;
    }

    .menu li {
      width: 16vh;
      height: 6.2vh;
      transition: background-position-x 0.9s linear;
      text-align: center;

      .options {
        font-size: 2.7vh;
        color: white;
        text-decoration: none;
        transition: all 0.45s;
        cursor: pointer;
        height: 4.9vh;
        font-family: Lato;
        font-weight: bold;
        overflow: hidden;
      }

      &:hover .options {
        color: gold;
      }
    }
  }

  /* Mobile styles */
  @media screen and (max-width: 768px) {
    .menu-button {
      display: block;
    }

    nav {
      display: none;
      width: 100%;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-color: rgba(19, 19, 19, 0.9);
      position: absolute;
      top: 10vh;
      left: 0;
      right: 0;
      padding: 2vh 0;

      &.open {
        display: flex;
      }

      .menu {
        flex-direction: column;
        align-items: center;
        margin-top: 0;
      }

      .menu li {
        margin-bottom: 2vh;
      }
    }
  }
}