.para {
    color: black;

    h2 {
        // font-size: 1rem;
        font-weight: 900;
        margin-bottom: 10vh;
        margin-top: 3vh;
    }

    p {
        // font-size: 1.1rem;
        letter-spacing: 0.02rem;
        font-weight: 400;
        line-height: 1.4;
        margin-bottom: 2vh;
        text-align: left;
    }
}

.blocks {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 6vh;
    grid-row-gap: 3.2vh;
    padding: 0 2.4vh 5vh;
    justify-content: center;

    img {
        max-width: 100%;
        align-self: center;
        display: block;
        width: 90%;
        margin: auto;
    }

    .para {
        color: black;
    }
}

// Media query to ensure responsiveness
@media (max-width: 768px) {
    .blocks {
        grid-template-columns: 1fr; // Stack elements
        // padding: 0 1.2vh; // Adjust padding

        h2 {
            // font-size: 2.5rem;
            font-weight: 900;
            margin-bottom: 5vh;
            margin-top: 3vh;
            text-align: center;
        }

        img {
            margin: 0 auto; // Center the image
        }
    }
}